import React from 'react';
import classes from './BuildControls.module.scss'

import BuildControl from './BuildControl/BuildControl'  //button to control

const controls = [
  { label: 'Salad', type: 'salad'},
  { label: 'Cheese', type: 'cheese'},
  { label: 'Bacon', type: 'bacon'},
  { label: 'Meat', type: 'meat'}
]

const buildControls = ({ingredientAdded, ingredientRemove, disabled, price, purchasable, ordered}) => {
  return(
    <div className={classes.BuildControls}>
        <p>Current Price : Rs- <strong>{price}</strong>/-</p>
      {
        controls.map(ctrl => {
          return <BuildControl 
                    key={ctrl.label} 
                    label={ctrl.label} 
                    added={ingredientAdded.bind(this, ctrl.type)}
                    remove={ingredientRemove.bind(this, ctrl.type)}
                    disabled={disabled[ctrl.type]} />
        })
      }
        <button className={classes.OrderButton} onClick={ordered} disabled={!purchasable}>ORDER NOW</button>
    </div>
  )
}

export default buildControls;