import React, { Component } from 'react'

import Aux from '../../hoc/Auxi/Aux'

//whole burger
import Burger from '../../components/Burger/Burger'  //burger only
import BuildControls from '../../components/Burger/BuildControls/BuildControls'
  
//modal
import Modal from '../../components/UI/Modal/Modal'

//order summary
import OrderSummary from '../../components/Burger/OrderSummary/OrderSummary'

//db
import axios from '../../axiosOrder'
//spinner
import Spinner from '../../components/UI/Spinner/Spinner'

//hoc
// import withErrorHandle from '../../hoc/withErrorHandle/withErrorHandle'

const ingredientPrice = {
  salad: 20,
  cheese: 25.5,
  bacon: 25,
  meat: 40
}

class BurgerBuilder extends Component {
  state = { 
    ingredients: null,
      totalPrice: 70,
      purchaseable: false,
      purchasing: false,
      loading: false
   }

   //get data from db 
   componentDidMount = () => {
     axios.get('https://burger-b2e94.firebaseio.com/ingredients.json')
      .then(res => {
        this.setState({ ingredients: res.data})
      })
      console.log(this.props, ' Burger builder')
   }
   

   //build controls - add nd delete
   addIngredientHandker = (type) => {
    const oldCount = this.state.ingredients[type] //salad, cheese, bacon , meat
    const updateCount = oldCount + 1; // while click add +1 to ingrdients
    const updateIngredients = {...this.state.ingredients}  //copy our state ingredients

    updateIngredients[type] = updateCount  // get our particular ingredient nd add +1 to it 
    
    const priceAddition = ingredientPrice[type]
    const newPrice = this.state.totalPrice + priceAddition

    this.setState({ ingredients: updateIngredients, totalPrice: newPrice });

    this.updatePurchaseState(updateIngredients)
  }

   removeIngredientHandler = (type) => {
    const oldCount = this.state.ingredients[type] //salad, cheese, bacon , meat
    if(oldCount <= 0){
      return false
    }
    const updateCount = oldCount - 1; // while click minus -1 to ingrdients
    const updateIngredients = {...this.state.ingredients}  //copy our state ingredients

    updateIngredients[type] = updateCount  // get our particular ingredient nd add +1 to it 
    
    const priceDeduction = ingredientPrice[type]
    const newPrice = this.state.totalPrice - priceDeduction

    this.setState({ ingredients: updateIngredients, totalPrice: newPrice });

    this.updatePurchaseState(updateIngredients)
   }

   updatePurchaseState = (ingredient) => {
     const ingredients = ingredient

     const sum = Object.keys(ingredients).map(igKey => {
       return ingredients[igKey]
     }).reduce((sum , ele) => {
       return sum + ele
     }, 0)

     this.setState({ purchaseable: sum > 0})
   }

   purchaseHandler = () => {
     this.setState({ purchasing: true });
   }
   closedHandle = () => {
     this.setState({ purchasing: false });
   }
   purchaseContinueHandler = () => {
    
    const queryParams = []
    for(let i in this.state.ingredients){
      queryParams.push(encodeURIComponent(i)+ '=' + encodeURIComponent(this.state.ingredients[i]))
    }
    queryParams.push('price='+ this.state.totalPrice)
    const queryString = queryParams.join('&')
    this.props.history.push({
      pathname: '/checkout',
      search: '?' + queryString
    })
   }
  render() { 
    const disableInfo = {
      ...this.state.ingredients
    }
    for(let i in disableInfo){
      disableInfo[i] = disableInfo[i] <= 0
    }
    let orderSummary = null;
    let burger = <Spinner/>
    if(this.state.ingredients){
      burger = (
        <Aux>
          <Burger ingredients={this.state.ingredients}/>

          <BuildControls 
            ingredientAdded={this.addIngredientHandker} 
            ingredientRemove={this.removeIngredientHandler}
            disabled={disableInfo}
            purchasable={this.state.purchaseable}
            ordered={this.purchaseHandler} 
            price={this.state.totalPrice.toFixed(2)} />
        </Aux>
      )
      orderSummary =  <OrderSummary 
                            ingredients={this.state.ingredients} 
                            purchaseCancel={this.closedHandle}
                            purchaseContinued = {this.purchaseContinueHandler}
                            price = {this.state.totalPrice} />
    }
    
        if(this.state.loading){
          orderSummary = <Spinner/>
        }
    return ( 
      <Aux>
        <Modal show={this.state.purchasing} modelClosed={this.closedHandle}>
            {orderSummary}     
        </Modal>
        {burger}
      </Aux>
     );
  }
}
 
export default BurgerBuilder;