import React from 'react'
import classes from './Order.module.scss'

const Order = (props) => {
  const ingredients = Object.keys(props.ingredients).map(ingredient => {
    return <span style={{textTransform: 'capitalize', display: 'inline-block', margin: '0 8px', border:'1px solid #ccc', padding: '5px'}} key={ingredient}>{ingredient} : {props.ingredients[ingredient]}</span>
  })
 return(
    <div className={classes.Order}>
      <p>Ingredients : {ingredients}</p>
      <p>Price: {Number.parseFloat(props.price).toFixed(2)}</p>
    </div>
  )
}

export default Order;