import React, { Component } from 'react'
import { Route } from 'react-router-dom'

import CheckoutSummary from '../../components/Order/CheckoutSummary/CheckoutSummary'
import ContactData from './ContactData/ContactData'

class Checkout extends Component {
  state = { 
    ingredients: {
      salad: 0,
      cheese: 0,
      bacon: 0,
      meat: 0
    },
    price: 0
   }

   componentDidMount() {
     const query = new URLSearchParams(this.props.location.search)
     const ingredients= {}
     let price = 0;
     for(let i of query.entries()){
       if(i[0] === 'price'){
         price = i[1]
       }
       else{
        ingredients[i[0]] = +i[1]
       }
     }
     this.setState({ ingredients, price });
   }
   checkoutCancelledHandler = () => {
    this.props.history.goBack()
   }

   checkoutContinuedHandler = () => {
    this.props.history.replace('/checkout/contact')
   }

  render() { 
    return ( 
      <div>
        <CheckoutSummary 
          ingredients={this.state.ingredients}
          checkoutCancelled={this.checkoutCancelledHandler}
          checkoutContinued={this.checkoutContinuedHandler} />
        {/* <Route path={this.props.match.path + '/contact'} component={ContactData}/> */}
        <Route path={this.props.match.path + '/contact'} render={(props) => <ContactData {...props} price={this.state.price} ingredients={this.state.ingredients} />}/> {/* for passing ingrdient */} 
      </div>
     );
  }
}
 
export default Checkout;