import React, { Component } from 'react';
import classes from './ContactData.module.scss'

import axios from '../../../axiosOrder'

import Spinner from '../../../components/UI/Spinner/Spinner'
import Button from '../../../components/UI/Button/Button'

//contact data
import Input from '../../../components/UI/Input/Input'

class ContactData extends Component {
  state = { 
    orderForm: {
      name: {
        elementType: 'input',
        elementConfig: {
          type: 'text',
          placeholder: 'Your Name'
        },
        value: '',
        validation: {
          required: true
        },
        valid: false
      },
      street: {
        elementType: 'input',
        elementConfig: {
          type: 'text',
          placeholder: 'Street'
        },
        value: '',
        validation: {
          required: true
        },
        valid: false
      },
      zipCode: {
        elementType: 'input',
        elementConfig: {
          type: 'text',
          placeholder: 'ZIP CODE'
        },
        value: '',
        validation: {
          required: true,
          minLength: 6,
          maxLength: 6
        },
        valid: false
      },
      country: {
        elementType: 'input',
        elementConfig: {
          type: 'text',
          placeholder: 'Your City'
        },
        value: '',
        validation: {
          required: true
        },
        valid: false
      },
      city: {
        elementType: 'input',
        elementConfig: {
          type: 'text',
          placeholder: 'Your Country'
        },
        value: '',
        validation: {
          required: true
        },
        valid: false
      },
      email: {
        elementType: 'input',
        elementConfig: {
          type: 'email',
          placeholder: 'Your Email'
        },
        value: '',
        validation: {
          required: true
        },
        valid: false
      },
      deliveryMethod: {
        elementType: 'select',
        elementConfig: {
          options: [
             {value: 'fastest', displayValue: 'Fastest'},
             {value: 'cheapest', displayValue: 'Cheapest'},
          ]
        },
        value: '',
        validation: {
          required: true
        },
        valid: false
      }
    },
      loading: false
   }

   checkValid = (val, rules) => {
    let isValid = true

    if(rules.required){
      isValid = val.trim() !== '' && isValid;
    }
    if(rules.minLength){
      isValid = val.length >= rules.minLength && isValid
    }
    if(rules.maxLength){
      isValid = val.length <= rules.maxLength && isValid
    }
    return isValid;
   }  

   orderHandler = (e) => {
     e.preventDefault()
     console.log(this.props.ingredients)
     ////////////////////////////////////
      this.setState({ loading: true });
      const formData = {};

      for(let formElement in this.state.orderForm){
        formData[formElement] = this.state.orderForm[formElement].value
      }

      const order = {
        ingredients: this.props.ingredients,
        price: this.props.price,
        date: new Date(),
        orderData: formData
      }
      axios.post('/orders.json', order)
        .then(res => this.setState({ loading: false}), this.props.history.push('/'))
        .catch(err => this.setState({ loading: false}))
   }

inputChangeHandler = (e, inputIdentifier) => {
  // console.log(e.target.value)
  const updateOrderForm = {
    ...this.state.orderForm
  }
  const updateFormElement= {
    ...updateOrderForm[inputIdentifier]
  }
  updateFormElement.value = e.target.value
  updateFormElement.valid = this.checkValid(updateFormElement.value, updateFormElement.validation)
  console.log(updateFormElement)
  updateOrderForm[inputIdentifier] = updateFormElement

  this.setState({ orderForm: updateOrderForm });
}

  render() { 
    const forElementsArray = [];
    for (let i in this.state.orderForm){
        forElementsArray.push({
          id: i,
          config: this.state.orderForm[i]
        })
    }
    let form = (
        <form onSubmit={this.orderHandler}>
          <Input elementType='...' elementConfig='...' />
            {
              forElementsArray.map(formElement => {
                return <Input 
                        key={formElement.id}
                        elementType={formElement.config.elementType}
                        elementConfig={formElement.config.elementConfig}
                        value={formElement.config.value}
                        changed={(event) => this.inputChangeHandler(event, formElement.id)} />
              })
            }
          <Button btnType="Success">ORDER</Button>
        </form>
    )
    if(this.state.loading){
      form = <Spinner/>
    }
    return ( 
      <div className={classes.ContactData}>
        <h4>Enter your contact data</h4>
        {form}
      </div>
     );
  }
}
 
export default ContactData;