import React from 'react';
import classes from './OrderSummary.module.scss'

import Aux from '../../../hoc/Auxi/Aux'

import Button from '../../UI/Button/Button'

const orderSummary = ({ingredients, purchaseCancel, purchaseContinued, price}) => {
  const ingrdientSummary = Object.keys(ingredients).map(ingredient => {
    return <li key={ingredient}><span style={{textTransform:'capitalize'}}>{ingredient}</span> : {ingredients[ingredient]}</li>
  })
  return (
    <Aux className={classes.Order}>
      <h3>Your Order</h3>
      <p>A delicious burger with the following ingrdients :-</p>
      <ul>
        {ingrdientSummary}
      </ul>
      <p><strong>Total Price : Rs.{price.toFixed(2)}</strong></p>
      <p>Continue to checkout ?</p>
      <Button btnType="Danger" clicked={purchaseCancel}>CANCEL</Button>
      <Button btnType="Success" clicked={purchaseContinued}>CONTINUE</Button>
    </Aux>
  )
}

export default orderSummary;